<template>
  <nav class="navbar navbar-expand navbar-light bg-light fixed-top">
      <div class="container">
          <router-link to="/" class="navbar-brand">Home</router-link>
          <div class="collapse navbar-collapse">
                <ul class="navbar-nav ml-auto" v-if="!user">
                    <li class="nav-item">
                        <router-link  class="nav-link" to="/login" >Login</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link  class="nav-link" to="/register" >Sign Up</router-link>
                    </li>
                </ul>

                <ul class="navbar-nav ml-auto" v-if="user">
                    <li class="nav-item">
                        <a  class="nav-link" href="javascript:void(0)" @click="handleLogout" >Logout</a>
                    </li>
                </ul>
          </div>
      </div>
  </nav>
</template>


<script>
import { mapGetters } from "vuex";
export default {
  name: 'Navbar',
//   props:['user'], // for getting component directive
  methods:{
    handleLogout()
    {
        localStorage.removeItem('access_token');
        localStorage.removeItem('user_ref');
        this.$store.dispatch('user',null);
        this.$router.push('/login');
    }
  },
  computed:{
      ...mapGetters(['user']) // if you use vuex you don't need to handle props
  }
}
</script>
